/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-lf-image-box:not(.editor-only) {
  padding: 0.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); }
  .wp-block-lf-image-box:not(.editor-only) > div {
    border-radius: 25px; }
  .wp-block-lf-image-box:not(.editor-only) > div:not(:last-child) {
    margin-right: 0.75rem; }
  .wp-block-lf-image-box:not(.editor-only).align-center p,
  .wp-block-lf-image-box:not(.editor-only).align-center h4 {
    text-align: center; }
  .wp-block-lf-image-box:not(.editor-only).align-left p,
  .wp-block-lf-image-box:not(.editor-only).align-left h4 {
    text-align: left; }
  .wp-block-lf-image-box:not(.editor-only).align-right p,
  .wp-block-lf-image-box:not(.editor-only).align-right h4 {
    text-align: right; }
  .wp-block-lf-image-box:not(.editor-only).columns-1 .column:not(:nth-child(1)) {
    display: none; }
  .wp-block-lf-image-box:not(.editor-only).columns-2 .column:nth-child(3),
  .wp-block-lf-image-box:not(.editor-only).columns-2 .column:nth-child(4) {
    display: none; }
  .wp-block-lf-image-box:not(.editor-only).columns-3 .column:nth-child(4) {
    display: none; }
  .wp-block-lf-image-box:not(.editor-only) .column {
    position: relative;
    height: var(--image-box-height);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-mask-image: -webkit-radial-gradient(#fff, #000);
    box-shadow: 0 5px 5px 0 rgba(18, 63, 82, 0.035), 0 0 0 1px rgba(176, 181, 193, 0.2);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 0;
    overflow: hidden; }
    .wp-block-lf-image-box:not(.editor-only) .column.column-1 {
      background-image: var(--image-box-img1); }
    .wp-block-lf-image-box:not(.editor-only) .column.column-2 {
      background-image: var(--image-box-img2); }
    .wp-block-lf-image-box:not(.editor-only) .column.column-3 {
      background-image: var(--image-box-img3); }
    .wp-block-lf-image-box:not(.editor-only) .column.column-4 {
      background-image: var(--image-box-img4); }
    .wp-block-lf-image-box:not(.editor-only) .column * {
      transition: all 0.3s ease-in; }
    .wp-block-lf-image-box:not(.editor-only) .column:hover .image-box-overlay {
      opacity: 0.7; }
    .wp-block-lf-image-box:not(.editor-only) .column:hover .image-box-content {
      transition: all 0.4s cubic-bezier(0.84, 0.12, 0.59, 0.83) !important;
      transition-delay: .2s;
      transform: translateY(0); }
    .wp-block-lf-image-box:not(.editor-only) .column:hover .image-box-content p {
      background: black; }
  .wp-block-lf-image-box:not(.editor-only) .box-link {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 5; }
  .wp-block-lf-image-box:not(.editor-only) .image-box-overlay {
    background-color: #000;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0.1;
    border-radius: 25px;
    z-index: 2; }
  .wp-block-lf-image-box:not(.editor-only) .image-box-content {
    position: relative;
    z-index: 4;
    transform: translateY(100%);
    color: white; }
    .wp-block-lf-image-box:not(.editor-only) .image-box-content h4 {
      position: absolute;
      bottom: 100%;
      left: 0;
      right: 0;
      margin: 0;
      padding-top: 40px;
      padding-bottom: 20px;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, black 100%); }
    .wp-block-lf-image-box:not(.editor-only) .image-box-content p {
      opacity: 1;
      width: 100%;
      margin-bottom: 0;
      padding: 20px;
      background: rgba(0, 0, 0, 0.6); }
